const galleryMarkUp = document.querySelector('.gallery');

function createImgMarkUp({ preview: src1, original: src2, description: alt }) {
  return `
  <li class="gallery-item">
	<a class="gallery-link" href="${src2}">
		<img 
			class="gallery-image" 
			src="${src1}" 
			alt="${alt}" 
		/>
	</a>
</li>
`;
}

function createImgsMarkUp(elements) {
  return elements.map(el => createImgMarkUp(el)).join('');
}

galleryMarkUp.insertAdjacentHTML('afterbegin', createImgsMarkUp(images));

let gallery = new SimpleLightbox('.gallery a', {
  captions: true,
  captionsData: 'alt',
  captionPosition: 'bottom',
  captionDelay: 250,
  overlayOpacity: 0.8,
});
gallery.on('show.simplelightbox', function () {});
